.singlepost-section {
  background-color: #150f22;
  padding: 60px 0 120px 0;
}
.featured-heade {
  color: #fff;
  font-family: Urbanist;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 19.647px;
  margin: 0px;
}

.left-posts {
  width: 75%;
  .post {
    border-radius: 16.7px;
    padding: 19px 24px;
    border-image: linear-gradient(
        to right,
        rgba(101, 130, 255, 1),
        rgba(101, 130, 255, 0)
      )
      1;
    background: linear-gradient(0deg, #150f22 0%, #1b112b 100%);
    position: relative;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      bottom: -30px;
      left: -142px;
      width: 457.208px;
      height: 206.723px;
      border-radius: 457.208px;
      opacity: 0.48;
      background: linear-gradient(270deg, #54008c 13.44%, #fb0 119.38%);
      filter: blur(62.4px);
      z-index: 0;
      /* behind content */
    }

    /* ensure content is above gradient */
    .image-name-div,
    .post-title-desc,
    .divider,
    .like-share-div {
      position: relative;
      z-index: 1;
    }

    .image-name-div {
      h3 {
        color: #fff;
        font-family: Urbanist;
        font-size: 13.753px;
        font-weight: 500;
        line-height: 19.647px;
        margin: 0px;
      }

      h4 {
        color: rgba(255, 255, 255, 0.37);
        font-family: Urbanist;
        font-size: 13.753px;
        font-weight: 500;
        line-height: 19.647px;
        margin: 0px;
      }
    }

    .post-title-desc {
      h1 {
        color: #fff;
        font-family: Urbanist;
        font-size: 18px;
        font-weight: 500;
        line-height: 19.647px;
      }

      p {
        color: rgba(255, 255, 255, 0.59);
        font-family: Urbanist;
        font-size: 16px;
        font-weight: 400;
        line-height: normal;
        letter-spacing: 0.16px;
      }
    }

    .divider {
      height: 1px;
      background: #555;
      width: 100%;
    }

    .like-share-div {
      .share-div {
        color: rgba(255, 255, 255, 0.5);
        font-family: Urbanist;
        font-size: 13.753px;
        font-weight: 500;
        line-height: 19.647px;
        margin: 0px;
        display: flex;
        align-items: center;
        gap: 5px;
        cursor: pointer;
        position: relative;
        z-index: 1;
      }

      .like-div {
        color: #fff;
        font-family: Urbanist;
        font-size: 13.753px;
        font-style: normal;
        font-weight: 500;
        line-height: 19.647px;
      }
    }
  }
}
img.rounded-circle.me-2 {
  width: 40px;
  height: 40px;
}

.right-addpost {
  width: 25%;
  border-radius: 15px;
  border: 2px solid #c0c0c049;
  background: linear-gradient(180deg, #140e22 0%, #503888 100%);
  height: 239px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;

  h1 {
    color: #fff;
    text-align: center;
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    /* 125% */
  }

  h4 {
    color: #fff;
    text-align: center;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: 30px;
    /* 166.667% */
  }
}
.imgcontainer {
  height: 300px;
  img {
    height: 100%;
    width: auto;
  }
}
@media (max-width: 1000px) {
 .right-addpost{
  display: none;
 }
 .left-posts{
  width: 100%;
 }
}